@media only screen and (max-width: 1372px) {

  .section {

    display: grid;
    
    }
}



@media only screen and (max-width: 1056px) {
  ul {
    font-size: 15px;
    text-align: center;
  }
  .navbar img {
    width: 50px;
    height: 40px;
    text-align: center;
  }

  .header {
    display: block;
    width: 100%;
    text-align: center;

    
    
  }
  .section{

display: block;

  }

}
@media only screen and (max-width: 984px) {
  .heh44 {
    display: grid;
    grid-template-columns: repeat(2,1fr);
    text-align: center;
  }

  .footer {
    display: grid;

    text-align: center;
  }
}


@media only screen and (max-width: 670px) {
  .heh44 {
    display: grid;
    grid-template-columns: repeat(1,1fr);
    justify-content: center;
  }
  section {
    margin: 0 auto  ;
  }
  .card1 {
    width: 100%;
  }
  .footer {
    text-align: center;
  }
 
}




@media only screen and (max-width: 584px) {
